.MenuBtn {
  position: relative;
  width: 30px;
  height: 44px;
  text-indent: -9999px;
  overflow: hidden;
  border: none;
  background-color: rgba(0, 0, 0, 0); }
  .MenuBtn:focus {
    outline: 0; }
  .MenuBtn:before, .MenuBtn:after {
    content: "";
    width: 100%;
    height: 2px;
    border-radius: 4px;
    background-color: #ed1c23;
    position: absolute;
    left: 0;
    transform: translate(0, -50%); }
  .MenuBtn:before {
    top: calc(50% - 6px); }
  .MenuBtn:after {
    top: calc(50% + 6px); }
  .MenuBtn.close:before {
    transform: translate(0, calc(-50% + 6px)) rotate(45deg); }
  .MenuBtn.close:after {
    transform: translate(0, calc(-50% - 6px)) rotate(-45deg); }

.BtnThumb {
